<template>
	<view>

		<cu-custom bgColor="theme-bg-color" :isBack="false">
			<block slot="backText"><text>返回</text></block>
			<block slot="content"><text> 个人中心 </text></block>
		</cu-custom>
		<view class="person-head theme-bg-color">
			<cmd-avatar :src="userInfo.avatar" @click="Join('userinfo-edit')" size="lg"
				:make="{'background-color': '#fff'}"></cmd-avatar>
			<view class="person-head-box">
				<view class="person-head-nickname">{{userInfo.userName}}</view>
				<view class="person-head-username">手机号：{{userInfo.phone}} </view>
				<view style="font-size: 20upx;margin-top: 10upx;" class='cu-tag radius line-white'>
					信誉积分{{userInfo.score}}</view>

			</view>
		</view>
		<view class="person-list">
			<cmd-cell-item title="我的预约" slot-left arrow @click="Join('room-my')">
				<cmd-icon size="24" color="#368dff"></cmd-icon>
			</cmd-cell-item>


			<cmd-cell-item title="检查版本" addon="v1.0" slot-left arrow>
				<cmd-icon size="24" color="#368dff"></cmd-icon>
			</cmd-cell-item>
		</view>
		<button class="cu-btn block theme-bg-color margin-tb-sm lg" @click="loginOut">登出</button>

<!-- 		<view class="publish" @click="Publish">
			<text class="lg text-gray cuIcon-edit" style="color:white;font-size: 60upx;"></text>
			<text>每日打卡</text>
		</view> -->
		<footer-bar :currentIndex="2"></footer-bar>


	</view>



</template>

<script>
	import cmdAvatar from "@/components/cmd-avatar/cmd-avatar.vue"
	import cmdIcon from "@/components/cmd-icon/cmd-icon.vue"
	import cmdCellItem from "@/components/cmd-cell-item/cmd-cell-item.vue"
	import store from "@/store";
	import {
		mapGetters
	} from "vuex";
	export default {
		components: {
			cmdAvatar,
			cmdCellItem,
			cmdIcon
		},
		data() {
			return {

			}
		},
		computed: {
			...mapGetters(["userInfo"]),
		},
		onShow() {

			if (this.$comm.CheckToken()) {
				//doto 获取用户信息
				this.getUserInfo();
			}
		},
		methods: {
			async getUserInfo() {
				await this.$store.dispatch("UserInfoByToken");
				console.log("aa", this.userInfo);
			},
			async Publish() {
				var that=this;
				uni.showModal({
					content: "你确定要完成今日打卡吗?",
					success: async function(res) {
						if (res.confirm) {
							let url = `/user/clock?id`;
							let {success,message}=await that.$http.PostAsync(url, {});
						     that.$comm.ShowToast(message);
						}
					},
				})
			},
			onClick(item) {},
			loginOut() {
				this.$store.dispatch("LoginOut");
				uni.redirectTo({
					url: "/pages/login/login"
				})
			},
			async Join(type) {
				if (type == "room-my") {
					uni.navigateTo({
						url: "/pages/room/room-my"
					})
				} else if (type == "userinfo-edit") {
					uni.navigateTo({
						url: "/pages/user-center/userinfo-edit"
					})
				}



			},
			async TodayPunch() {
				let {
					success,
					message
				} = await this.$http.PostAsync("/Components/WxBase/TodayPunch", {});
				if (success) {
					this.$comm.ShowToast("打卡成功");
				} else {

					this.$comm.ShowToast(message);
				}
			}
		}
	}
</script>

<style scoped>
	.person-head {
		display: flex;
		flex-direction: row;
		align-items: center;
		height: 120px;
		padding-left: 20px;
		/* background: linear-gradient(to right, #36bbff, #36bbff); */
	}

	.person-head-box {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		margin-left: 10px;
	}

	.person-head-nickname {
		font-size: 18px;
		font-weight: 500;
		color: #fff;
	}

	.person-head-username {
		font-size: 14px;
		font-weight: 500;
		color: #fff;
	}

	.person-list {
		line-height: 0;
	}

	.b-fixed {
		position: fixed;
		bottom: 300upx;
		right: 20upx;

		height: 100upx;
		padding: 20upx;

	}

	.publish {
		display: flex;
		align-items: center;
		flex-direction: column;
		justify-content: center;
		background-color: #ff0000;
		width: 150upx;
		color: white;
		height: 150upx;
		border-radius: 50%;
		position: fixed;
		right: 30upx;
		bottom: 150upx;
	}
</style>
